import axios from 'axios';
import React, { useEffect, useState } from 'react'

const JobList = () => {

    const [job, setJob] = useState([])

    useEffect(() => {
        axios.get('http://localhost:4001/api/').then((res) => {
            setJob(res.data);
        });
    }, []);

    const edit = () => {
        window.confirm('No configurado')
    }
    const eliminar = (id) => {
        console.log(id)
        // axios.delete('http://localhost:4001/api/delete-student/' + id)
        //     .then(res => {
        //         console.log(res.data);
        //     })
        //     .catch((err) => {
        //         console.log(err);
        //     })
        // DELETE request using axios inside useEffect React hook
        // axios.delete('http://localhost:4001/api/delete-student/' + id)
        //     .then(res => {
        //         console.log(res.data);
        //     })
        //     .catch((err) => {
        //         console.log(err);
        //     })

        // empty dependency array means this effect will only run once (like componentDidMount in classes)

    }


    if (!job) return null;



    return (
        <div className="row">
            <div className="col-md-12">
                <table className="table table-striped">
                    <thead className="thead-dark">
                        <tr>
                            <th>Nombre</th>
                            <th>Email</th>
                            <th>Telefono</th>
                            <th className="text-center">Accion</th>
                        </tr>
                    </thead>
                    <tbody>
                        {job.map(job =>
                            <tr>
                                {/* <td>{job._id}</td> */}
                                <td>{job.name}</td>
                                <td>{job.email}</td>
                                <td>{job.phone}</td>
                                <td className="text-center">
                                    {/* <link
                                        to={ name: 'edit', params: { id: student._id } }
                                        className="btn btn-primary"
                                    >Edit
                                    </link> */}
                                    <button className=" me-2 btn btn-dark" onClick={eliminar(job._id)}>
                                        Eliminar
                                    </button>
                                    <button className="btn btn-dark" onClick={edit}>
                                        Editar
                                    </button>
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table >
            </div >
        </div >
    );
}

export default JobList
